import { useEffect, useState } from "react";
import { useRouter } from "next/router";
import axios from "axios";
// import Input from "@/components/Input"; // Assuming you have a reusable Input component

interface Movie {
  _id: string;
  name: string;
  singer: string;
  cast: string[];
  releaseDate: string;
  budget: number;
}

const Input: React.FC<InputProps> = ({ label, value, onChange, id, type }) => {
  const router = useRouter();
  //   const { id } = router.query; // Get the movie ID from the URL
  const [movie, setMovie] = useState<Movie | null>(null);
  const [loading, setLoading] = useState<boolean>(true);
  const [error, setError] = useState<string>("");
  const [name, setName] = useState<string>("");
  const [singer, setSinger] = useState<string>("");
  const [cast, setCast] = useState<string[]>([]);
  const [releaseDate, setReleaseDate] = useState<string>("");
  const [budget, setBudget] = useState<number>(0);

  useEffect(() => {
    if (id) {
      fetchMovie(id as string);
    }
  }, [id]);

  const fetchMovie = async (movieId: string) => {
    try {
      const res = await axios.get(`/api/movies/${movieId}`);
      if (res.status === 200) {
        const movieData = res.data;
        setMovie(movieData);
        setName(movieData.name);
        setSinger(movieData.singer || "");
        setCast(movieData.cast);
        setReleaseDate(movieData.releaseDate);
        setBudget(movieData.budget);
      } else {
        setError("Movie not found.");
      }
    } catch (error) {
      setError("Error fetching movie data.");
    } finally {
      setLoading(false);
    }
  };

  const handleSave = async () => {
    try {
      const updatedMovie = {
        name,
        singer,
        cast,
        releaseDate,
        budget,
      };

      await axios.put(`/api/movies/${id}`, updatedMovie);
      router.push("/"); // Redirect back to the home page or wherever you want
    } catch (error) {
      setError("Error updating movie.");
    }
  };

  if (loading) {
    return <div>Loading...</div>;
  }

  if (error) {
    return <div>{error}</div>;
  }

  return (
    <div className="p-4">
      <h1 className="text-2xl font-bold mb-4">Edit Movie</h1>
      <div className="space-y-4">
        <Input
          label="Movie Name"
          value={name}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setName(e.target.value)
          }
          id="movie-name"
          type="text"
        />

        <Input
          label="Singer"
          value={singer}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setSinger(e.target.value)
          }
          id="movie-singer"
          type="text"
        />
        <Input
          label="Cast"
          value={cast.join(", ")}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setName(e.target.value)
          }
          id="movie-cast"
          type="text"
        />

        <Input
          label="Release Date"
          value={releaseDate}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setName(e.target.value)
          }
          id="movie-releaseDate"
          type="date"
        />

        <Input
          label="Budget"
          value={budget}
          onChange={(e: React.ChangeEvent<HTMLInputElement>) =>
            setName(e.target.value)
          }
          id="movie-budget"
          type="number"
        />

        <button
          onClick={handleSave}
          className="bg-red-600 py-3 text-white rounded-md w-full mt-4 focus:bg-red-700 transition"
        >
          Save Changes
        </button>
      </div>
    </div>
  );
};
